.profile {

	&__inner {
		display: flex;
		flex-wrap: wrap;
		padding-left: 286px;
		position: relative;

		@media (max-width: 1200px) {
			padding-left: 0;
			flex-direction: column; } }

	&__img {
		position: absolute;
		top: 0;
		left: 0;
		width: 236px;
		height: 220px;
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;

		@media (max-width: 1200px) {
			position: static;
			margin-bottom: 20px; } }

	&__rating {
		margin-bottom: 10px; }

	&__subscription {
		padding-top: 40px;
		width: 300px;
		margin-left: auto;

		@media (max-width: 1200px) {
			margin-left: 0;
			width: 100%; } } }
