.timepicker {

    &-range {
        display: flex;
        flex-wrap: wrap;
        padding: 13px 15px 13px 50px;
        position: relative;
        border: 1px solid $colorLightGrey;
        border-radius: 5px;


        .form__input--timepicker-range {
            display: inline-block;
            border: 0;
            width: 50px;
            padding: 0;

            @media (max-width: 1200px) {
                width: 80px; } }

        &__icon {
            position: absolute;
            top: 13px;
            left: 20px;
            width: 18px;
            height: 18px;
            display: flex;
            justify-content: center;
            align-items: center;

            .icon {
                pointer-events: none; } }

        &__separator {
            margin: 0 10px; } } }
