.link {
	display: inline-block;
	text-decoration: none;
	color: $colorOrange;
	transition: all .3s;

	&:hover {
		color: $colorOrangeHover;
		text-decoration: underline; }

	&--black {
		color: #000; }

	&--blue {
		color: $colorLightBlue;

		&:hover {
			color: $colorBlue; } }

	&--with-icon {
		position: relative;
		padding-right: 40px;
		padding-top: 2px;

		.icon {
			position: absolute;
			top: 0;
			right: 0; }

		&:hover .icon {
			fill: $colorOrangeHover; } }

	&--cGrey {
		color: $colorCustomGrey;

		&:hover {
			text-decoration: none; } }

	&--dashed {
		border-bottom: 1px dashed $colorCustomGrey;

		&:hover {
			border-bottom-color: $colorOrangeHover; } }

	&--fz-13 {
		font-size: rem(13px); } }
