.button {
	display: inline-block;
	text-decoration: none;
	padding: 11px 20px;
	border: 1px solid #000000;
	background-color: #fff;
	border-radius: 6px;
	color: #000;
	font-size: rem(14px);
	font-weight: bold;
	text-align: center;
	box-sizing: border-box;
	transition: all .3s;

	&--min-w-150 {
		min-width: 150px; }

	&--min-w-188 {
		min-width: 188px; }

	&--min-w-216 {
		min-width: 216px; }

	&--min-w-226 {
		min-width: 226px; }

	&--min-w-243 {
		min-width: 243px; }

	&--min-w-296 {
		min-width: 296px;

		@media (max-width: 500px) {
			min-width: auto; } }

	&--min-w-303 {
		min-width: 303px; }

	&--min-w-320 {
		min-width: 320px;

		@media (max-width: 500px) {
			min-width: auto; } }

	&--m-0-auto {
		margin: 0 auto; }

	&--w-100p {
		width: 100%; }

	&:focus {
		outline: 0; }

	&:hover {
		border-color: $colorOrange;
		background-color: $colorOrange;
		color: #fff;

		.icon {
			fill: #fff; } }

	&--orange {
		background-color: $colorOrange;
		border-color: $colorOrange;
		color: #fff;

		&:hover {
			background-color: $colorOrangeHover;
			border-color: $colorOrangeHover; } }

	&--white {
		background-color: $colorWhite;
		color: #000;
		border-color: $colorLightGrey;

		&:hover {
			background-color: $colorLightPurple;
			border-color: $colorLightPurple;
			color: #000; } }

	&--green {
		background-color: $colorGreen;
		border-color: $colorGreen;
		color: #000;

		&:hover {
			background-color: $colorOrangeHover;
			border-color: $colorOrangeHover;
			color: #fff; } }

	&--grey {
		background-color: $colorButtonGrey;
		border-color: $colorLightGrey;
		color: #000;

		&:hover {
			background-color: $colorOrangeHover;
			border-color: $colorOrangeHover;
			color: #fff; } }

	&--dark-blue {
		background-color: $colorDarkBlue;
		border-color: $colorDarkBlue;
		color: #fff;

		&:hover {
			background-color: $colorOrangeHover;
			border-color: $colorOrangeHover;
			color: #fff; } }

	&--light-blue {
		background-color: $colorLightBlue;
		border-color: $colorLightBlue;
		color: #fff;

		&:hover {
			background-color: $colorOrangeHover;
			border-color: $colorOrangeHover;
			color: #fff; } }

	&--fz-15 {
		font-size: rem(15px) !important; }

	&__icon-box {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		vertical-align: middle;
		margin-right: 10px; }

	&__text {
		vertical-align: middle; } }

