.main {

	&__item {

		&--mb-50 {
			margin-bottom: 50px; }

		&--centered {
			margin-left: auto;
			margin-right: auto;

			.chat-bot {
				margin-left: auto;
				margin-right: auto; } }

		&--pt-30 {
			padding-top: 30px; }

		&--mb-9 {
			margin-bottom: 9px; }

		&--mb-20 {
			margin-bottom: 20px; }

		&--mb-25 {
			margin-bottom: 25px; }

		&--mb-30 {
			margin-bottom: 30px; }

		&--mb-36 {
			margin-bottom: 36px; }

		&--mb-40 {
			margin-bottom: 40px; }

		&--mb-42 {
			margin-bottom: 42px; }

		&--mb-56 {
			margin-bottom: 56px; }

		&--mb-60 {
			margin-bottom: 60px; }

		&--mb-65 {
			margin-bottom: 65px;

			@media (max-width: 600px) {
				margin-bottom: 30px; } }

		&--mb-70 {
			margin-bottom: 70px;

			@media (max-width: 600px) {
				margin-bottom: 35px; } }

		&--mb-80 {
			margin-bottom: 80px;

			@media (max-width: 600px) {
				margin-bottom: 40px; } }

		&--mb-146 {
			margin-bottom: 146px;

			@media (max-width: 1200px) {
				margin-bottom: 100px; }

			@media (max-width: 1024px) {
				margin-bottom: 40px; } } } }
