.title {

	&__text {
		font-weight: bold;
		font-size: rem(30px);
		line-height: rem(24px);

		&--large {
			font-weight: 900;
			font-size: rem(46px);
			line-height: rem(64px);

			@media (max-width: 991px) {
				font-size: rem(30px);
				line-height: rem(36px); } }

		&--extra-small {
			font-size: rem(16px);
			line-height: rem(19px); }

		&--small {
			font-size: rem(22px);
			line-height: rem(24px); }

		&--grey {
			color: $colorDarkGrey; }

		&--text-center {
			text-align: center; }

		&--m-0 {
			margin: 0; }

		&--mt-0 {
			margin-top: 0; }

		&--mb-20 {
			margin-bottom: 20px; }

		&--mb-37 {
			margin-bottom: 37px; } }

	&__text-box {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		.title__text {
			margin-left: auto;
			margin-right: auto; }

		&--mb-37 {
			margin-bottom: 37px; }

		&--pl-138 {
			padding-left: 138px; }

		@media (max-width: 1200px) {
			flex-direction: column;
			padding-left: 0;

			.title__text {
				margin-bottom: 10px; } } }


	&__description {
		text-align: center;
		font-size: rem(20px);
		line-height: rem(34px);

		&--small {
			font-size: rem(18px);
			line-height: rem(22px); } }

	&--simple {
		padding-bottom: 18px;
		border-bottom: 1px solid rgba(0,0,0,.2);
		margin-bottom: 38px; }

	&__favorites {

		.link {
			min-height: 22px;
			padding-top: 2px; } }

	&--mb-10 {
		margin-bottom: 10px; } }
