.search {
	width: 100%;
	position: relative;

	&--max-w-750 {
		max-width: 750px; }

	&--max-w-637 {
		max-width: 637px; }

	&--m-0-auto {
		margin: 0 auto; }

	&__field {
		position: relative; }

	&__input {
		display: block;
		width: 100%;
		border: 1px solid $colorGrey;
		border-radius: 8px;
		padding: 5px 20px 5px 60px;
		font-size: rem(19px);
		line-height: rem(38px);
		transition: all .3s;

		&::placeholder {
			color: #3E3E3E; }

		&:focus {
			outline: 0;
			border-color: $colorOrange; } }

	&__submit {
		display: block;
		position: absolute;
		padding: 0;
		top: 16px;
		left: 25px;
		width: 18px;
		height: 18px;
		background-color: transparent;
		border: 0;
		cursor: pointer;

		&:focus {
			outline: 0; }

		svg {
			width: 100%;
			height: 100%; } }

	&__result {
		display: none;
		width: 100%;
		position: absolute;
		top: 100%;
		left: 0;
		margin-top: 10px;
		background-color: #fff;
		z-index: 100;
		border: 1px solid $colorGrey;
		border-radius: 8px;
		padding: 20px; }

	&__result-item {
		margin-bottom: 15px;

		&:last-child {
			margin-bottom: 0; } }

	&__result-link {
		color: $colorBlack;
		text-decoration: none;
		font-weight: bold;
		transition: all .3s;

		&:hover {
			color: $colorOrangeHover; } } }
