.bar {

    &__inner {
        display: flex;
        flex-wrap: wrap;
        align-items: center; }

    &__list {
        display: flex;
        flex-wrap: wrap;

        &--centered {
            align-items:  center;
            justify-content: center; }

        &--with-add-new {
            width: calc(100% - 300px);
            margin-left: 150px;

            @media (max-width: 1200px) {
                width: 100%;
                margin-left: 0; } } }

    &__add-new {
        width: 150px;

        @media (max-width: 1200px) {
            width: 100%;
            text-align: right; } }

    &__link {
        display: block;
        min-width: 250px;
        padding: 10px 20px;
        border: 1px solid $colorLightGrey;
        border-right: 0;
        text-decoration: none;
        text-align: center;
        color: #000;
        font-weight: bold;
        font-size: rem(20px);
        line-height: rem(34px);
        transition: all .3s;

        &.active {
            background-color: $colorOrange;
            color: #fff;
            pointer-events: none; }

        &:hover {
            color: $colorOrangeHover; }

        @media (max-width: 1200px) {
            margin: 0 5px 10px; } }

    &__item {

        &:first-child {

            .bar__link {
                border-radius: 6px 0 0 6px;

                @media (max-width: 1200px) {
                    border-radius: 0; } } }

        &:last-child {

            .bar__link {
                border-right: 1px solid $colorLightGrey;
                border-radius: 0 6px 6px 0;

                @media (max-width: 1200px) {
                    border-radius: 0; } } } } }
