.services {

	&__list {
		display: flex;
		flex-wrap: wrap;
		margin-left: -12px;
		margin-right: -12px;

		&--mb-45 {
			margin-bottom: 45px; }

		.button {
			position: relative;
			padding-right: 40px;
			padding-left: 40px; }

		.icon--services {
			position: absolute;
			top: calc(50% - 9px);
			right: 18px;
			width: 18px;
			height: 18px;

			svg {
				width: 100%;
				height: 100%; } } }

	&__item {
		width: calc(33.3333% - 12px);
		margin: 0 6px 22px;

		@media (max-width: 991px) {
			width: calc(50% - 12px); }

		@media (max-width: 600px) {
			width: calc(100% - 12px); }

		.button {
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			padding-top: 12px;
			padding-bottom: 12px;
			font-weight: bold;
			font-size: rem(20px);
			line-height: rem(34px); } } }
