.filter {

    &__list {
        display: flex;
        flex-wrap: wrap;
        margin-left: -30px;
        margin-right: -30px;
        overflow: hidden;

        @media (max-width: 1200px) {
            margin-left: 0;
            margin-right: 0; } }

    &__item {
        width: calc(33.3333% - 60px);
        margin: 0 30px 20px;

        @media (max-width: 1200px) {
            width: 100%;
            margin-left: 0;
            margin-right: 0; } }

    &__item-name {
        font-weight: bold;
        margin-bottom: 15px; }

    &__select {

        .select {
            width: 100%; }

        .select2-container .select2-selection--single {
            height: 60px;
            padding: 18px 0; }

        .select2-container--default .select2-selection--single .select2-selection__arrow {
            top: 16px; } } }
