.menu {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -20px;

	&__link {
		color: $colorBlack;
		padding: 5px 20px;
		display: block;
		text-decoration: none;
		position: relative;
		transition: all .3s;

		&:after {
			content: '';
			width: 100%;
			position: absolute;
			bottom: 0;
			left: 0;
			height: 2px;
			background-color: $colorOrange;
			transform: scale(0,1);
			transition: all .3s; }

		&:hover:after {
			transform: scale(1,1); } } }
