.subscription {

	&__title {
		font-weight: bold;
		font-size: rem(16px);
		line-height: rem(24px);
		margin-bottom: 18px; }

	&__list {

		.list__item {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: 23px;

			&:last-child {
				margin-bottom: 0; } }

		.form__filed {
			margin-right: 8px; } }

	&__name {
		font-size: rem(14px);
		line-height: rem(17px);
		margin-right: 25px;
		max-width: calc(100% - 105px); }



	&__delete {
		font-size: rem(12px);
		line-height: rem(15px);
		color: $colorLigthDarkGrey;
		text-decoration: none;
		margin-top: 2px;
		transition: all .3s;

		&:hover {
			color: $colorOrangeHover; } } }


