.chat-bot {
    width: 400px;
    border: 1px solid $colorExtraLightGrey;
    border-radius: 4px;
    overflow: hidden;

    a {
        color: $colorBlue;
        text-decoration: underline;

        &:hover {
            color: $colorOrangeHover; } }

    @media (max-width: 500px) {
        width: 100%; }

    &__result {
        text-align: left;
        font-size: rem(14px);
        line-height: rem(25px); }

    &__title {
        background-color: $colorOrange;
        padding: 18px 30px;
        color: #fff;
        font-weight: bold;
        text-transform: uppercase; }

    &__content {
        padding: 24px 30px;
        height: 360px;
        overflow: auto;
        text-align: right; }

    &__answer {
        display: inline-block;
        width: 240px;
        padding: 10px 20px;
        background-color: $colorLightPurple;
        margin-bottom: 26px;
        text-align: center;
        font-size: rem(14px);
        line-height: rem(25px);
        border-radius: 50px; }

    &__option {
        color: $colorBlue; }

    &__question {
        font-size: rem(14px);
        line-height: rem(25px);
        margin-bottom: 26px;
        text-align: left;

        &--main {
            font-size: rem(16px);
            line-height: rem(25px);

            .chat-bot__question-text {
                color: $colorLightBlue;
                margin-bottom: 22px; }

            .chat-bot__option {
                color: $colorDarkBlue;
                text-decoration: underline;
                margin-bottom: 11px;

                &:last-child {
                    margin-bottom: 0; } } }

        &:last-child {
            margin-bottom: 0; } }

    &__menu {
        display: none;
        width: 100%; }

    &__footer {
        border-top: 1px solid $colorExtraLightGrey;
        padding: 10px 15px; }

    &__menu-opener {
        width: 24px;
        height: 24px;
        margin-right: 6px;
        display: flex;
        justify-content: center;
        align-items: center;

        &:hover {

            .icon {
                fill: $colorOrangeHover; } }

        .icon {
            fill: $colorExtraLightGrey; }

        .icon--menu-show {
            display: none; }

        &.opened {

            .icon--menu-show {
                display: block; }

            .icon--menu-hide {
                display: none; } } }


    &__form {

        .form {
            display:  flex;
            flex-wrap: wrap;
            align-items: center; } }

    &__submit-box {
        width: 114px; }

    &__ipnut-box {
        width: calc(100% - 150px);
        margin-right: 6px; }

    &__input {
        display: block;
        border: 0;
        width: 100%;
        padding: 5px 15px;

        &:focus {
            outline: 0; }

        &::-moz-placeholder {
            color: #BDBDBD;
            font-size: 14px; }
        &::-webkit-input-placeholder {
            color: #BDBDBD;
            font-size: 14px; }
        &:-ms-input-placeholder {
            color: #BDBDBD;
            font-size: 14px; }
        &::-ms-input-placeholder {
            color: #BDBDBD;
            font-size: 14px; }
        &::placeholder {
            color: #BDBDBD;
            font-size: 14px; } }

    &__menu-list {
        display:  flex;
        flex-wrap: wrap;
        margin-left: -7px;
        margin-right: -7px;
        margin-top: 20px; }

    &__menu-item {
        width: calc(50% - 14px);
        margin-left: 7px;
        margin-right: 7px;
        margin-bottom: 3px;

        .button {
            width: 100%; }

        @media (max-width: 500px) {
            width: calc(100% - 14px); } } }


