.modal {
    width: 100%;
    max-width: 750px;
    padding: 43px 77px 74px;
    background-color: #fff;
    margin-left: auto;
    margin-right: auto;
    position: relative;

    @media (max-width: 1200px) {
        padding: 30px; }

    &__closer {
        display: block;
        width: 25px;
        height: 25px;
        font-size: 25px;
        position: absolute;
        top: 20px;
        right: 20px;
        line-height: 1;
        color: #000;
        text-decoration: none;
        transition: all .3s;

        &:hover {
            color: $colorOrangeHover; } }

    &__title {
        font-weight: 900;
        font-size: rem(30px);
        line-height: rem(36px);
        text-align: center;
        margin-bottom: 25px; } }
