.breadcrumbs {

    &__list {
        display: flex;
        flex-wrap: wrap; }

    &__item {
        padding-right: 24px;
        position: relative;

        &:after {
            content: '-';
            position: absolute;
            top: 0;
            right: 10px;
            color: #000;

            &:last-child {
                display: none; } }

        &:last-child {
            padding-right: 0;

            .breadcrumbs__link {
                pointer-events: none;
                color: #000; } } }

    &__link {
        color: $colorOrange;
        text-decoration: none;
        font-weight: 500;
        font-size: rem(18px);
        transition: all .3s;

        &:hover {
            color: $colorOrangeHover; }

        @media (max-width: 1024px) {
            font-size: rem(14px);
            line-height: rem(16px); } } }




