.burger {

	&__opener {
		display: block;
		width: 26px;
		height: 16px;
		position: relative;

		&:after {
			content: '';
			width: 100%;
			height: 2px;
			background-color: $colorOrange;
			position: absolute;
			bottom: 0;
			left: 0; }

		&:before {
			content: '';
			width: 100%;
			height: 2px;
			background-color: $colorOrange;
			position: absolute;
			top: 0;
			left: 0; } }

	&__line {
		width: 100%;
		height: 2px;
		background-color: $colorOrange;
		position: absolute;
		top: calc(50% - 1px);
		left: 0; }

	&__top {
		display: flex;
		flex-wrap: wrap; }

	&__close {
		width: 25px;
		height: 25px;
		font-size: 25px;
		line-height: 1;
		color: #000;
		text-decoration: none;
		margin-left: auto;
		transition: all .3s;

		&:hover {
			color: $colorOrangeHover; } }

	&__logo {
		margin-bottom: 15px;

		.logo {
			display: flex;
			justify-content: center;
			align-items: center; }

		.logo__img-box {
			display: inline-block;
			margin: 0 auto; } } }
