.select2 {

	&-container {
		width: 100% !important; }

	&:focus {
		outline: 0; }

	&-selection:focus {
		outline: 0; }

	&-container .select2-selection--single {
		height: 33px;
		padding: 3px 0; }

	&-container--default .select2-selection--single .select2-selection__rendered {
		line-height: 26px; }

	&-container--default .select2-selection--single .select2-selection__arrow b {
		border: 0;
		width: 7px;
		height: 7px;
		border-left: 2px solid #000;
		border-bottom: 2px solid #000;
		transform: rotate(-45deg);
		transform-origin: center;
		top: 50%;
		left: calc(50% - 3px); }

	&-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b {
		border-left: 2px solid #000;
		border-bottom: 2px solid #000;
		transform: rotate(135deg);
		top: calc(50% + 2px); }

	&-container--default .select2-selection--single {
		border-color: #DBDEE7; }

	&-container--default .select2-results__option[aria-selected=true] {
		background-color: $colorOrange;
		color: #fff; }

	&-container--default .select2-results__option--highlighted[aria-selected] {
		color: $colorOrange;
		background-color: transparent; }

	&-container--default .select2-results__option[aria-selected=true][aria-selected] {
		color: #fff;
		background-color: $colorOrange; }

	&-dropdown {
		margin-top: 5px;
		border: 1px solid #DBDEE7 !important;
		border-radius: 4px !important;
		position: relative; }

	&-container .select2-selection--single .select2-selection__rendered {
		padding-left: 13px;
		padding-right: 30px; }

	&-results__option {
		padding: 8px 13px; }

	&-container--default.select2-container--open.select2-container--below .select2-selection--single,
	&-container--default.select2-container--open.select2-container--below .select2-selection--multiple {
		border-radius: 4px; }

	&-container--default .select2-selection--single .select2-selection__arrow {
		right: 8px; } }
