.aside {

    &__item {
        margin-bottom: 30px; }

    &__item-title {
        background-color: $colorOrange;
        border-radius: 4px 4px 0 0;
        color: #fff;
        padding: 15px 20px;

        &--simple {
            background-color: #fff;
            border: 1px solid $colorExtraLightGrey;
            border-bottom: 0;
            color: #000;
            font-weight: bold;
            padding-bottom: 0; } }

    &__item-main {
        border: 1px solid $colorExtraLightGrey;
        border-top: 0;
        border-radius: 0 0 4px 4px;
        padding: 18px 20px 26px; } }
