.more {

    &__inner {
        display: none;
        width: 260px;
        background-color: #fff;
        border: 1px solid $colorLightGrey;
        border-radius: 8px;
        position: absolute;
        top: calc(100% + 10px);
        left: 0;
        z-index: 200;
        overflow: hidden; }

    &__opener {
        display: block;
        width: 16px;
        height: 16px;
        position: relative;

        &:before,
        &:after {
            content: '';
            display: block;
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background-color: $colorLigthDarkGrey;
            position: absolute;
            left: calc(50% - 2px);
            transition: all .3s; }

        &:before {
            top: 0; }

        &:after {
            bottom: 0; }

        &:hover {

            &:after,
            &:before,
            .more__opener-dot {
                background-color: $colorOrangeHover; } } }

    &__opener-dot {
        display: block;
        width: 4px;
        height: 4px;
        border-radius: 50%;
        background-color: $colorLigthDarkGrey;
        position: absolute;
        left: calc(50% - 2px);
        top: calc(50% - 2px);
        transition: all .3s; }

    &__item {
        display: block;
        width: 100%;
        padding: 14px 24px;
        border-bottom: 1px solid $colorLightGrey;
        font-size: rem(18px);
        line-height: rem(32px);
        text-decoration: none;
        color: #000;
        transition: all .3s;

        &:last-child {
            border-bottom: 0; }

        &:hover {
            background-color: $colorOrangeHover;
            color: #fff; } } }
