.notifications {

	&__list {
		display: flex;
		flex-wrap: wrap;
		margin-left: -25px;
		margin-right: -25px;

		@media (max-width: 1200px) {
			margin-left: -15px;
			margin-right: -15px; } }

	&__item {
		width: calc(25% - 50px);
		margin: 0 25px 25px;

		@media (max-width: 1200px) {
			width: calc(50% - 30px);
			margin-left: 15px;
			margin-right: 15px; }

		@media (max-width: 600px) {
			width: calc(100% - 30px); }

		.form__filed {
			display: flex;
			flex-wrap: wrap; }

		.form__label {
			margin-right: 8px; } } }

