.results-item {
    padding-top: 30px;
    padding-bottom: 47px;
    border-top: 1px solid $colorGrey;
    border-bottom: 1px solid $colorGrey;

    &__title {
        font-weight: 500;
        font-size: rem(24px);
        line-height: rem(32px);
    }

    &__content {
        margin-top: 16px;
        font-size: rem(16px);
        line-height: rem(24px);
    }

    &__label {
        margin-top: 24px;
        font-size: rem(14px);
        line-height: rem(32px);
        color: $colorCustomGrey;
    }

    .breadcrumbs {
        margin-top: 23px;
    }
}