.file {

    &__inner {
        display: flex;
        flex-wrap: wrap; }

    &__name {
        padding: 10px 16px;
        width: calc(100% - 130px);
        border: 1px solid $colorLightGrey;
        border-right: 0;
        border-radius: 5px 0 0 5px;

        &--placeholder {
            color: #8B8B8B; } }

    &__field {
        width: 130px;
        position: relative;
        cursor: pointer;

        &:hover .file__btn {
            background-color: $colorOrangeHover; } }

    &__input-box {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        z-index: 10; }

    &__input {
        width: 100%;
        height: 100%;
        display: block; }

    &__btn {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $colorOrange;
        color: #fff;
        padding: 10px 12px;
        border-radius: 0 5px 5px 0;
        border: 1px solid $colorOrange;
        transition: all .3s;

        &:hover {
            background-color: $colorOrangeHover; } } }
