.table {
    min-width: 1100px;

    &-wrapper {
        width: 100%;
        overflow-x: auto; }

    &__cell {
        padding: 5px 15px;
        font-size: rem(11px);
        line-height: rem(14px);
        border-bottom: 1px solid $colorExtraLightGrey;

        &--head {
            text-transform: uppercase;
            color: $colorLigthDarkGrey; } }

    .col-name {
        width: 200px; }

    .col-phone-work {
        width: 170px; }

    .col-phone-mobile {
        width: 170px; }

    .col-company {
        width: 120px; }

    &--simple {
        border: 1px solid $colorLightGrey;

        .table__cell {
            padding: 20px 10px;
            border: 1px solid $colorLightGrey;
            font-size: rem(16px);
            line-height: rem(20px);

            &--empty {
                text-align: center; } }

        .table__cell--head {
            color: #000;
            padding: 10px 50px 10px 20px;
            position: relative;
            background-color: #F5F8FD;
            border: 1px solid $colorLightGrey;
            text-align: left;
            font-weight: bold;
            font-size: rem(18px);
            line-height: rem(20px);
            text-transform: none; }

        .table__sort {
            width: 22px;
            height: 22px;
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: calc(50% - 11px);
            right: 15px;

            &.up {

                .icon {
                    fill: #000;

                    &:hover {
                        fill: $colorOrangeHover; } } }

            &.down {
                transform: scale(1,-1);

                .icon {
                    fill: #000;

                    &:hover {
                        fill: $colorOrangeHover; } } }

            .icon {
                fill: #DADADA;

                &:hover {
                    fill: $colorOrangeHover; } } }

        .table__more {
            position: absolute;
            top: calc(50% - 8px);
            left: 4px; }

        .table__cell--number {
            position: relative;
            padding-left: 40px; }

        .table__cell--head-number {
            padding-left: 40px; } } }


