.rater {
	font-size: 24px;
	line-height: 1;
	display: flex;
	align-items: center;

	&--events-none {
		pointer-events: none; }

	.rate-base-layer {
		color: $colorOrange; }

	.rate-select-layer,
	.rate-hover-layer {
		color: $colorOrangeHover; }

	&__value {
		font-size: rem(16px);
		line-height: rem(18px);
		margin-bottom: 8px;
		margin-left: 16px; } }
