.links {

    &__list {
        display: flex;
        flex-wrap: wrap;
        margin-left: -20px;
        margin-right: -20px;

        &--decorated {
            padding: 17px 50px;
            background-color: $colorLightPurple;
            border-radius: 6px;

            @media (max-width: 1200px) {
                margin-left: 0;
                margin-right: 0; } } }

    &__item {
        width: calc(33.3333% - 40px);
        margin: 20px;

        .link {
            font-weight: 700;
            font-size: rem(20px);
            line-height: rem(30px); }

        @media (max-width: 1024px) {
            width: calc(50% - 40px); }

        @media (max-width: 767px) {
            width: calc(100% - 40px); } } }
