.bid {

	&--with-aside {
		display: flex;
		flex-wrap: wrap;

		@media (max-width: 767px) {
			flex-direction: column; } }

	&__bar {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 30px;


		&--2-in-row {
			margin-left: -12px;
			margin-right: -12px;

			@media (max-width: 600px) {
				margin-bottom: 0; }

			.bid__bar-item {
				width: calc(50% - 24px);
				margin: 0 12px 24px;
				position: relative;

				@media (max-width: 600px) {
					width: calc(100% - 24px); }

				.button {
					width: 100%;
					padding: 20px 50px; } } }

		&-count {
			position: absolute;
			top: 17px;
			right: 17px;
			width: 28px;
			height: 28px;
			display: flex;
			justify-content: center;
			align-items: center;
			background-color: $colorBlue;
			color: #fff;
			border-radius: 6px; } }

	&__list {
		display: flex;
		flex-wrap: wrap;
		margin-right: -15px;
		margin-left: -15px; }


	&__item {
		width: calc(33.3333% - 30px);
		margin: 0 15px 30px;
		background-color: $colorLightPurple;
		border-radius: 6px;
		padding: 48px 36px;
		box-sizing: border-box;
		transition: all .3s;

		@media (max-width: 991px) {
			width: calc(50% - 30px); }

		@media (max-width: 600px) {
			width: calc(100% - 30px); }

		&:hover {
			background-color: $colorOrange;

			.bid__icon {
				border-color: $colorWhite;

				svg {
					fill: $colorWhite; } }

			.bid__name {
				color: $colorWhite; }

			.bid__description {
				color: $colorWhite; } } }


	&__icon {
		width: 56px;
		height: 56px;
		border-radius: 50%;
		border: 1px solid $colorLightGrey;
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 auto 12px;

		svg {
			width: 21px;
			height: 21px;
			fill: $colorOrange; } }

	&__name {
		text-align: center;
		font-weight: bold;
		font-size: rem(20px);
		line-height: rem(34px);
		transition: all .3s; }

	&__description {
		text-align: center;
		margin-bottom: 40px;
		font-size: rem(18px);
		line-height: rem(32px);
		transition: all .3s; }

	&__item-footer {
		display: flex;
		flex-wrap: wrap;
		justify-content: center; }

	&__table {

		&--mb-70 {
			margin-bottom: 70px; } }

	&__aside {
		width: 260px;
		margin-left: 20px;

		@media (max-width: 767px) {
			margin-left: 0;
			width: 100%; } }

	&__main {
		width: calc(100% - 280px);

		@media (max-width: 767px) {
			width: 100%;
			margin-bottom: 20px; } }

	&__footer {
		width: 100%;
		margin-top: 60px; }

	&__footer-btns {
		display:  flex;
		flex-wrap: wrap; }

	&__footer-btn {
		margin-right: 22px;
		margin-bottom: 22px;

		.button {
			font-size: rem(19px);
			line-height: rem(32px); } } }


