* {
	padding: 0;
	margin: 0;
	box-sizing: border-box; }

html {
	font-size: 10px; }

body {
	font-size: rem(16px);
	font-family: 'Inter', Arial, sans-serif; }

.container {
	width: 100%;
	max-width: 1140px;
	padding: 0 15px;
	box-sizing: border-box;
	margin: 0 auto; }

.scroll-off {
	overflow: hidden; }

.overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 0;
	visibility: hidden;
	transform: scale(0);
	pointer-events: none;
	background-color: rgba(0,0,0,.4);
	z-index: 500;
	transition: opacity .3s;

	&.opened {
		opacity: 1;
		transform: scale(1);
		visibility: visible;
		pointer-events: all; } }

.text-blue {
	color: $colorBlue; }

