.header {
	padding: 17px 77px;

	@media (max-width: 1164px) {
		padding-left: 15px;
		padding-right: 15px; }

	&__inner {
		display: flex;
		flex-wrap: wrap;
		align-items: center; }

	&__logo {
		margin-right: 5%;

		@media (max-width: 1400px) {
			margin-right: auto; } }

	&__search {
		margin-right: 7%;

		@media (max-width: 1400px) {
			margin-right: 15px; } }

	&__search-link {
		display: block;
		width: 22px;
		height: 22px; }

	&__menu {
		margin: 0 auto;

		@media (max-width: 1400px) {
			order: 3;
			margin: 0 0 15px;

			.menu {
				flex-direction: column;
				margin: 0; }

			.menu__item {
				margin-bottom: 10px; }

			.menu__link {
				width: 100%;
				display: inline-block;
				text-decoration: none;
				padding: 11px 20px;
				border: 1px solid $colorOrange;
				border-radius: 6px;
				font-size: 1.4rem;
				font-weight: bold;
				text-align: center;
				box-sizing: border-box;
				background-color: $colorOrange;
				color: #fff;
				transition: all .3s;

				&:hover {
					background-color: $colorOrangeHover;
					border-color: $colorOrangeHover; } } } }

	&__user {
		margin-left: 3%;

		@media (max-width: 1400px) {
			order: 2;
			margin-left: 0;
			margin-bottom: 25px;

			.user__name {
				font-size: rem(14px);
				line-height: rem(16px); }

			.user__dropdown {
				width: 100%;
				position: static;
				padding: 10px 0 0; } } }


	&__theme {
		width: 150px;

		@media (max-width: 1400px) {
			width: 100%;
			order: 4;

			.theme .select {
				width: 100%; } } }

	&__burger {
		display: none;

		@media (max-width: 1400px) {
			display: block; } }


	&__burger-content-scroll {
		flex: 1 1 auto;

		@media (max-width: 1400px) {
			display: block;
			width: 300px;
			height: 100vh;
			position: fixed;
			top: 0;
			left: 0;
			background-color: $colorWhite;
			overflow: auto;
			transform: translateX(-100%);
			z-index: 600;
			transition: all .3s;

			&.opened {
				transform: translateX(0); } } }

	&__burger-content {
		display: flex;
		flex-wrap: wrap;
		flex: 1 1 auto;

		@media (max-width: 1400px) {
			display: flex;
			flex-direction: column;
			padding: 20px; } }

	&__burger-hidden {
		display: none;

		@media (max-width: 1400px) {
			display: block; }

		&--main {
			order: 1; } } }



