.results-sort {
    font-size: rem(14px);
    line-height: rem(32px);

    &__row {
        display: flex;
        flex-wrap: wrap; }

    &__item {
        &:not(:last-child)::after {
            content: " | ";
            white-space: pre; }

        &--active {
            pointer-events: none;

            .link {
                color: $colorBlack; } } } }
