.comments {

    &__item {
        margin-bottom: 14px; }

    &__content {
        padding-left: 52px;
        font-size: rem(14px);
        line-height: rem(19px); }

    &__item-top {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin-bottom: 24px;

        @media (max-width: 767px) {
            flex-direction: column;
            align-items: flex-start; } }

    &__user {

        .user {
            padding-top: 3px;
            padding-left: 52px;
            position: relative;
            display: flex;
            flex-wrap: wrap;
            min-height: 36px; }

        .user__avatar {
            position: absolute;
            top: 0;
            left: 0; } }

    &__date {
        margin-left: auto;
        font-size: rem(14px);
        line-height: rem(19px);
        color: $colorLigthDarkGrey;

        @media (max-width: 767px) {
            padding-left: 52px;
            margin-left: 0;
            margin-bottom: 5px; } }

    &__files {
        display: flex;
        flex-wrap: wrap;
        margin-top: 10px;
        font-size: rem(12px);
        line-height: rem(16px); }

    &__files-title {
        font-weight: bold;
        margin-right: 10px; }

    &__file {
        margin-bottom: 5px; }

    &__pagination {
        margin-top: 56px;
        margin-bottom: 25px; } }
