.user {

	&__avatar {
		width: 36px;
		height: 36px;
		border-radius: 50%;
		background-color: $colorOrange;
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;

		&.empty {
			background-size: 17.18px 17.18px; } }

	&__name {
		font-size: rem(18px);
		line-height: rem(32px); }

	&__outer {
		position: relative; }

	&__box {
		position: relative;
		padding-left: 52px;
		padding-right: 25px;
		display: flex;
		align-items: center;
		min-height: 36px;

		.user__avatar {
			position: absolute;
			top: 0;
			left: 0; }

		.user__opener {
			width: 13px;
			height: 8px;
			display: flex;
			align-items: center;
			justify-content: center;
			position: absolute;
			top: calc(50% - 4px);
			right: 0;

			&:hover svg {
				fill: $colorOrange; } } }

	&__dropdown {
		display: none;
		position: absolute;
		top: 100%;
		right: 0;
		width: 100%;
		background-color: #fff;
		padding: 10px 0 10px 52px;
		box-sizing: border-box; }

	&__item {
		margin-bottom: 10px;

		&:last-child {
			margin-bottom: 0; } } }
