.datepicker {

    &-range {
        position: relative;

        &__input-box {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            z-index: 10;

            .form__input--date-range {
                width: 100%;
                height: 100%;
                display: block; } }

        &__inner {
            display: flex;
            border-radius: 5px;
            border: 1px solid $colorLightGrey;
            padding: 13px 15px;
            position: relative;
            z-index: 1; }

        &__from {
            position: relative;
            padding-left: 30px; }

        &__icon {
            position: absolute;
            left: 0;
            top: 0;
            width: 18px;
            height: 18px;
            display: flex;
            align-items: center;
            justify-content: center; }

        &__separator {
            margin: 0 10px; }

        &__text {
            min-width: 88px;

            &--text-center {
                text-align: center; } } } }


