.pagination {

    &__inner {
        display: flex;
        flex-wrap: wrap;

        &--centered {
            justify-content: center;
            align-items: center; } }

    &__btn {
        color: #000;
        text-decoration: none;
        text-transform: uppercase;
        transition: all .3s;

        &:hover {
            color: $colorOrangeHover; }

        &--prev {
            margin-right: 10px; }

        &--next {
            margin-left: 10px; } }

    &__list {
        display: flex;
        flex-wrap: wrap;

        .pagination__link {
            display: block;
            text-decoration: none;
            color: #000;
            font-size: rem(21px);
            padding: 6px 12px;
            margin: 0 3px;
            border-radius: 8px;
            transition: all .3s;

            &.active {
                background-color: $colorOrange;
                color: #fff;
                font-weight: 700;
                pointer-events: none; }

            &:hover {
                color: $colorOrangeHover; }

            @media (max-width: 767px) {
                padding: 3px 6px; } } }

    &__arrow {
        width: 47px;
        height: 47px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $colorOrange;
        transition: all .3s;

        &.disabled {
            pointer-events: none;
            opacity: .5; }

        .icon {
            fill: #fff; }

        &:hover {
            background-color: $colorOrangeHover; }

        &--prev {
            margin-right: 10px; }

        &--next {
            margin-left: 10px; } } }



