.footer {
	position: relative;
	border-top: 1px solid #999999;
	padding-top: 50px;
	margin-top: 60px;

	@media (max-width: 600px) {
		padding-bottom: 20px; }

	&__bar {
		position: absolute;
		top: -47px;
		right: 40px;
		background-color: $colorLightPurple;
		padding: 12px 14px;
		box-sizing: border-box;

		@media (max-width: 1164px) {
			right: 15px; } }

	&__bar-list {
		display: flex;
		flex-wrap: wrap;
		min-width: 180px;
		justify-content: center;
		margin-right: -15px;
		margin-left: -15px; }

	&__bar-item {
		margin-left: 15px;
		margin-right: 15px; }

	&__bar-link {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		text-decoration: none;

		&:hover .icon {
			fill: $colorOrangeHover; } }

	&__bar-count {
		margin-left: 4px;
		color: $colorExtraLightGrey;
		font-weight: 500;
		font-size: 16px;
		text-decoration: none; }

	&__inner {
		display: flex;
		flex-wrap: wrap;

		@media (max-width: 600px) {
			flex-direction: column;
			align-items: center; } }

	&__left {
		margin-right: auto;

		@media (max-width: 600px) {
			margin-right: 0;
			margin-bottom: 20px; } }

	&__right {
		text-align: right;

		@media (max-width: 600px) {
			text-align: center; } }

	&__logo {
		margin-bottom: 30px;

		@media (max-width: 600px) {
			margin-bottom: 15px;

			.logo {
				display: flex;
				justify-content: center; }

			.logo__img-box {
				display: inline-block;
				margin: 0 auto; } } }

	&__copyright {
		font-size: rem(12px);
		line-height: rem(18px); }

	&__phone-title {
		font-weight: bold;
		font-size: rem(18px);
		line-height: rem(32px); }

	&__phone a {
		text-decoration: none;
		color: #000000;
		font-size: rem(18px);
		line-height: rem(32px);
		transition: all .3s;

		&:hover {
			color: $colorOrangeHover; } }

	&__email a {
		text-decoration: none;
		color: #000000;
		font-size: rem(18px);
		line-height: rem(32px);
		transition: all .3s;

		&:hover {
			color: $colorOrangeHover; } } }
