.acco {

    &__content {
        display: none; }

    &__item {
        border-radius: 10px;
        padding: 15px 50px 15px 25px;
        position: relative;

        &--extra-extra-light-grey {
            background-color: $colorExtraExtraLightGrey; }

        &[data-mode="opened"] .acco__opener {
            transform: rotate(0deg); } }

    &__title {
        font-weight: bold;
        font-size: rem(20px); }

    &__opener {
        position: absolute;
        top: 12px;
        right: 25px;
        transform: rotate(180deg);
        transform-origin: center;
        transition: all .3s; }

    &--user-data {
        margin-bottom: 48px; } }
