.data {

	&__item {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 14px;

		@media (max-width: 600px) {
			flex-direction: column; }

		&:last-child {
			margin-bottom: 0; } }

	&__name {
		width: 170px;
		margin-right: 10px;

		@media (max-width: 600px) {
			width: 100%;
			margin-right: 0;
			margin-bottom: 5px; } }

	&__file {
		margin-bottom: 5px; }

	&--simple {

		.data__item {
			margin-bottom: 24px; }

		.data__name {
			font-weight: bold;
			width: 155px;

			@media (max-width: 600px) {
				width: 100%;
				margin-bottom: 5px; } }

		.data__value {
			width: calc(100% - 165px);

			@media (max-width: 600px) {
				width: 100%; } } }

	&--small {

		.data__name {
			width: 100%;
			font-size: rem(12px);
			color: $colorLigthDarkGrey;
			margin-right: 0;
			margin-bottom: 5px; } } }


