.employees {

    &__table {
        margin-bottom: 18px; }

    &__footer {
        display: flex;
        flex-wrap: wrap;
        font-size: rem(12px);
        line-height: rem(14px);

        @media (max-width: 767px) {
            flex-direction: column; } }

    &__total {
        margin-right: 6%;
        text-transform: uppercase;

        @media (max-width: 767px) {
            margin-right: 0;
            margin-bottom: 10px; } }

    &__pages {
        text-transform: uppercase;
        margin-right: 20px;

        @media (max-width: 767px) {
            margin-right: 0;
            margin-bottom: 10px; } } }

