.form {

	&__row {
		display: flex;

		&:not(:first-child) {
			margin-top: 30px; }

		&--desktop {

			@media (max-width: 1200px) {
				display: block;

				& > .form__col {
					margin: 0;
					width: 100%;

					&:not(:first-child) {
						margin-top: 30px; } } } }

		&--mobile {

			@media (max-width: 767px) {
				display: block;

				& > .form__col {
					margin: 0;
					width: 100%;

					&:not(:first-child) {
						margin-top: 30px; } } } }

		&--jc--sb {
			justify-content: space-between; } }

	&__col {

		&--w--50p {
			width: calc((100% - 90px) / 2);

			@media (max-width: 1200px) {
				width: calc((100% - 40px) / 2); } }

		&--w--30p {
			width: 30%; }

		&--fs--0 {
			flex-shrink: 0; }

		&--fg--1 {
			flex-grow: 1; }

		&--ml--90 {
			margin-left: 90px; }

		&--ml--140 {
			margin-left: 140px; }

		&--pos--rel {
			position: relative; } }

	&__frame {
		padding: 25px 40px;
		background-color: white;
		border: 1px solid $colorGrey;
		border-radius: 6px;

		@media (max-width: 767px) {
			padding-left: 20px;
			padding-right: 20px; }

		&:not(:first-child) {
			margin-top: 20px; }

		&--tooltip {

			@media (min-width: 1201px) {
				position: absolute;
				bottom: 0;
				left: 0;
				transform: translateX(calc(-100% - 20px));

				&::before {
					position: absolute;
					top: 10%;
					right: 0;
					content: "";
					display: block;
					$size: 8px;
					width: $size;
					height: $size;
					background-color: white;
					border-top: 1px solid $colorGrey;
					border-right: 1px solid $colorGrey;
					transform: translateX(calc(50% + 1px)) rotate(45deg); } } }

		&-text {
			font-size: rem(15px);
			line-height: rem(25px);

			&--mb--30 {
				margin-bottom: 30px; } } }

	&__input {
		border: 1px solid $colorLightGrey;
		border-radius: 8px;
		padding: 13px 15px;
		width: 100%;

		&--timepicker {

			&::-webkit-calendar-picker-indicator {
				background: none; } }

		&--timepicker-range {

			&::-webkit-calendar-picker-indicator {
				background: none; } }

		&--datepicker-range {
			width: 276px;

			&::-webkit-calendar-picker-indicator {
				background: none; }

			@media (max-width: 767px) {
				width: 100%; } }

		&--date {
			width: 168px;
			padding-left: 50px;

			&::-webkit-calendar-picker-indicator {
				background: none; }

			@media (max-width: 767px) {
				width: 100%; } }

		&--radio {
			position: absolute;
			top: 0;
			left: 0;
			width: 0;
			height: 0;
			visibility: hidden;
			pointer-events: none; }

		&--radio ~ .form__label {
			display: inline-block;
			width: 17px;
			height: 17px;
			border-radius: 50%;
			border: 1px solid $colorLightGrey;
			position: relative;
			cursor: pointer;
			margin-right: 10px; }

		&--radio:checked ~ .form__label {
			border: 4px solid $colorOrange; }

		&--checkbox {
			position: absolute;
			top: 0;
			left: 0;
			width: 0;
			height: 0;
			visibility: hidden;
			pointer-events: none; }

		&--checkbox ~ .form__label {
			display: inline-block;
			width: 20px;
			height: 20px;
			border: 1px solid $colorExtraLightGrey;
			border-radius: 6px;
			position: relative;
			cursor: pointer;
			vertical-align: middle;

			&:after {
				content: '';
				width: 4px;
				height: 8px;
				border-top: 2px solid $colorOrange;
				border-left: 2px solid $colorOrange;
				position: absolute;
				top: 2px;
				left: 6px;
				opacity: 0;
				transform: rotate(-135deg);
				transition: all .3s; } }

		&--checkbox:checked ~ .form__label:after {
			opacity: 1; }

		&--checkbox ~ .form__label ~ .form__input-text {
			display: inline-block;
			width: calc(100% - 38px);
			margin-left: 10px; }

		&--checkbox ~ .form__label ~ .form__input-text--default {
			display: inline;
			width: initial; }

		&:focus {
			outline: 0; }

		&--w-668 {
			width: 668px;

			@media (max-width: 1200px) {
				width: 100%; } }

		&--w-376 {
			width: 376px;

			@media (max-width: 1200px) {
				width: 100%; } }

		&--w-160 {
			width: 160px;

			@media (max-width: 1200px) {
				width: 100%; } } }

	&__input-text {

		&--extra-small {
			font-size: rem(12px); } }

	&__line {
		display: flex;
		flex-wrap: wrap;

		&--rater {
			margin-bottom: 30px;

			.form__name {
				width: auto;
				margin-right: 10px;
				margin-bottom: 0; } }

		&--jc-sb {
			justify-content: space-between; }

		&--mb-32 {
			margin-bottom: 32px; }

		@media (max-width: 1200px) {
			flex-direction: column;

			&--with-counter {
				flex-direction: row;

				.form__line-box--text {
					width: calc(100% - 297px); } } }

		@media (max-width: 767px) {
			&--with-counter {
				flex-direction: column;

				.form__line-box--text {
					width: 100%; } } } }

	&__name {
		width: 175px;
		margin-right: 40px;

		@media (max-width: 1200px) {
			width: 100%;
			margin-bottom: 10px;
			margin-right: 0; }

		&--no-mobile {

			@media (max-width: 767px) {
				display: none; } }

		&--w-auto {
			width: auto; }

		&--w-100p {
			width: 100%; }

		&--mr-10 {
			margin-right: 10px; }

		&--mr-0 {
			margin-right: 0; }

		&--mb-8 {
			margin-bottom: 8px; }

		&--mb--10 {
			margin-bottom: 10px; }

		&--mb-13 {
			margin-bottom: 13px; }

		&--mb-30 {
			margin-bottom: 30px; }

		&--with-tooltip {
			position: relative;
			padding-left: 37px;

			.form__tooltip {
				position: absolute;
				top: 0;
				left: 0; } }

		&--large {
			font-weight: bold;
			font-size: rem(18px);
			margin-bottom: 10px;
			width: 100%; }

		&--fz-16 {
			font-size: rem(16px); } }

	&__name-value {
		font-weight: bold;

		&--text-center {
			text-align: center; }

		&--fw-normal {
			font-weight: normal; } }

	&__item {

		&--ai-c {
			display: flex;
			align-items: center; }

		&--mr-88 {
			margin-right: 88px; }

		&--w-1of3 {
			width: calc(1 / 3 * (100% - 142px));

			@media (max-width: 1200px) {
				width: initial; } }

		&--w-2of3 {
			width: calc(2 / 3 * (100% - 142px));

			@media (max-width: 1200px) {
				width: initial; } }

		&--w-1of2 {
			width: calc(1 / 2 * (100% - 88px));

			@media (max-width: 1200px) {
				width: initial;

				&:first-child {
					margin-bottom: 32px; } } }

		&--p-relative {
			position: relative; }

		&--mb-42 {
			margin-bottom: 42px; }

		&--mb-36 {
			margin-bottom: 36px; }

		&--mb-17 {
			margin-bottom: 17px; }

		&--mt-20 {
			margin-top: 20px; }

		&--frame {
			padding: 25px 40px;
			border: 1px solid $colorGrey;
			border-radius: 6px; }

		&--max-w-430 {
			max-width: 430px; }

		&--with-arrow {
			position: relative;

			@media (max-width: 1200px) {
				position: initial; }

			&::before {
				position: absolute;
				top: 10%;
				right: 0;
				content: "";
				display: block;
				$size: 8px;
				width: $size;
				height: $size;
				background-color: white;
				border-top: 1px solid $colorGrey;
				border-right: 1px solid $colorGrey;
				transform: translateX(calc(50% + 1px)) rotate(45deg);

				@media (max-width: 1200px) {
					display: none; } } }

		&--tooltip {
			position: absolute;
			bottom: 0;
			left: 0;
			transform: translateX(calc(-100% - 20px));

			@media (max-width: 1200px) {
				position: initial;
				bottom: auto;
				left: auto;
				transform: none; } } }

	&__field-footer {
		margin-top: 5px; }

	&__line-box {
		display: flex;
		flex-wrap: wrap;

		&--mr-25 {
			margin-right: 25px; }

		@media (max-width: 1200px) {
			margin-bottom: 10px;

			&--counter {
				width: 272px; } }

		@media (max-width: 767px) {
			&--counter {
				width: 100%; } } }

	&__textarea {
		border: 1px solid $colorLightGrey;
		border-radius: 8px;
		width: 100%;
		height: 138px;
		resize: none;

		&--w-668 {
			width: 668px; }

		&:focus {
			outline: 0;
			padding: 10px 15px; }

		@media (max-width: 1200px) {
			width: 100%; } }

	&__field {

		@media (max-width: 1200px) {
			width: 100%;

			&--datepicker-range {
				width: 276px; }

			&--timepicker-range {
				width: 272px; } }

		@media (max-width: 767px) {
			&--datepicker-range {
				width: 100%; }

			&--timepicker-range {
				width: 100%; } }

		&--with-icon {
			position: relative;

			.form__field-icon {
				position: absolute;
				top: 15px;
				left: 20px; }

			.icon {
				pointer-events: none; } }

		&--normal {

			.form__label {
				vertical-align: middle; }

			.form__input-text {
				vertical-align: middle;
				width: auto !important; } }

		&--mb--20 {
			margin-bottom: 20px; }

		&--mb--10 {
			margin-bottom: 10px; }

		&--checkboxes {
			width: calc(100% - 215px);

			@media (max-width: 1200px) {
				width: 100%; } }

		&--w--100p {
			width: 100%; } }

	&__field-icon {
		width: 18px;
		height: 18px;
		display: flex;
		align-items: center;
		justify-content: center;
		pointer-events: none; }

	&__select {
		width: 376px;

		+ .select2-container .select2-selection--single {
			padding: 10px;
			height: 48px; }

		+ .select2-container--default .select2-selection--single .select2-selection__arrow {
			top: 8px; }

		@media (max-width: 767px) {
			width: 100%; }

		&-option {
			display: flex;
			align-items: center;

			&--selection {

				.form__select-image {
					width: 25px;
					height: 25px; } }

			&--result {

				.form__select-image {
					width: 30px;
					height: 30px; } } }

		&-image {
			margin-right: 14px;
			flex-shrink: 0;
			border-radius: 50%;
			overflow: hidden; }

		&-img {
			display: block;
			max-width: 100%;
			max-height: 100%; }

		&-text {
			overflow: hidden;
			text-overflow: ellipsis; } }

	&__checkbox {
		margin-bottom: 12px;

		&:last-child {
			margin-bottom: 0; } }

	&__radio {
		margin-bottom: 12px;

		&:last-child {
			margin-bottom: 0; } }

	&__file {
		width: 446px;
		margin-bottom: 10px;

		&:last-child {
			margin-bottom: 0; }

		@media (max-width: 1200px) {
			width: 100%;
			font-size: rem(14px); } }

	&__files-footer {
		margin-top: 10px; }

	&__footer-btns {
		display: flex;
		flex-wrap: wrap; }

	&__footer-btn {
		margin-right: 23px;
		margin-bottom: 23px;

		&:last-child {
			margin-bottom: 0; }

		&--large .button {
			font-size: rem(18px); } }

	&__footer {

		&--modal {
			margin-top: 28px; }

		&--mt-22 {
			margin-top: 22px; } }

	&__inner {
		&--row {
			display: flex;

			@media ( max-width: 1200px ) {
				flex-wrap: wrap; } } } }


